.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  display: grid;
}

.container {
  padding: 20px;
}

.container1 {
  display: flex;
  justify-content: center;
  border: 1px solid gray;
  width: 380px;
  height: 460px;
  padding: 20px;
  margin: 20px;
}

.container2 {
  display: flex;
  justify-content: center;
  justify-items: center;
  align-items: center;
  align-content: center;
  width: 420px;
  margin: 20px;
}

.tab {
  background-color: #282c34;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
